<template>
  <div class="map-view">
    <div id="main"></div>
  </div>
</template>

<script>
import BMap from "BMap";
export default {
  name: "MapView",
  data() {
    return {
      // 百度地图的数据
      mapData: {
        title: {},
      },
    };
  },
  mounted() {
    // 百度地图API功能
    var map = new BMap.Map("main"); // 创建Map实例
    map.centerAndZoom(new BMap.Point(116.404, 39.915), 12); // 初始化地图,设置中心点坐标和地图级别
    map.enableScrollWheelZoom(true); // 允许滚轮缩放
    var marker = new BMap.Marker(new BMap.Point(116.404, 39.915)); // 创建标注
    map.addOverlay(marker); // 将标注添加到地图中
    // 异步加载数据
    this.loadData();
  },
  methods: {
    loadData() {
      // 异步加载数据
      this.mapData = {
        title: {
          text: "百度地图演示",
        },
      };
    },
  },
};
</script>

<style lang="less" scoped>
.map-view {
  width: 100%;
  #main {
    width: 100%;
    height: 600px;
  }
}
</style>